<template>
  <div class="app">
    <div class="header header-subheader">الگوی</div>
    <div class="header">{{ patternNames[id].split('.')[0].split('-').join(' ') }}</div>
    <img :src="patternFiles[id]" />
    <router-link to="/">
      <button class="btn btn-primary">{{ back }}</button>
    </router-link>
    <button class="btn btn-outline">{{ examples }}</button>
    <div class="content">
      <div v-for="(datum, index) in data" :key="index">
        <img v-if="photoRegex.test(datum)" :src="urlToImg(datum.split('photo:')[1])" />
        <div v-else>{{ datum }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";

export default {
  name: "Patterns",
  props: ["id"],
  data() {
    return {
      data: null
    };
  },
  computed: {
    ...mapState(["patternNames", "back", "examples", "photoRegex"]),
    ...mapGetters(["patternFiles"])
  },
  methods: {
    urlToImg: url => {
      return require(`../assets/data/img/${url}`);
    }
  },
  beforeMount() {
    let json = require(`../assets/data/${
      this.patternNames[this.id].split(".")[0]
    }.json`);
    this.data = json.content;
  }
};
</script>

